@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cookie&display=swap);
#nav {
    font-family: 'Muli', sans-serif;
    font-size: 20px;
    padding-bottom: 0;
    padding-top: 0;
    border-bottom: 0;
    border-top: 0;
}

#title{
    font-family: 'Cookie', cursive;
    font-size: 40px;
}

#nav-link{
    display: flex;
    
}

#img-icon {
    width: 40px;
    margin-right: 20px;
}
#title-home {
    font-family: 'Cookie', cursive;
    font-size: 60px;
    color: white; 
}

#home{
    background-image: url(/portfolio/static/media/bg-code.07ad91e1.jpeg);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;  
    
}
#img-profile{
    width: 300px;
    border-radius: 50%;
}

#color{
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(81, 156, 241, 0.541)), to(rgba(227, 126, 240, 0.911)));
