    background-image: linear-gradient(rgba(81, 156, 241, 0.541), rgba(227, 126, 240, 0.911));
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 40px;

}

#mini-bio{
    width: 50%;
    align-content: center;
}

p{
    font-family: 'Muli', sans-serif;
    color: white;
    justify-self: center;
}

#sobre{
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
}

.div-sobre{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.title-sobre {
    font-family: 'Cookie', cursive;
    font-size: 60px;
    color: #343a40; 
}

.icon-img {
    width: 30px;
    margin-right: 20px;
}

