    flex-direction: column;
    height: 500px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

#title-hab{
    font-family: 'Cookie', cursive;
    font-size: 60px;
    color: white;
    justify-content: center;
}

#div-btn{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.btn{
    margin: 20px;
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
}


#blog{
    background-image: url(/portfolio/static/media/nebula.f905085d.jpg);
    background-repeat: no-repeat;
    background-position: center;
    height: 600px;
}

#div-blog{
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(81, 156, 241, 0.4)), to(rgba(227, 126, 240, 0.4)));
    background-image: -webkit-linear-gradient(rgba(81, 156, 241, 0.4), rgba(227, 126, 240, 0.4));
    background-image: linear-gradient(rgba(81, 156, 241, 0.4), rgba(227, 126, 240, 0.4));
    height: 600px;

}

#title-blog{
    font-family: 'Cookie', cursive;
    font-size: 60px;
    color: white;
    justify-content: center;
}

#div-blog{
    display: flex;  
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#cards{
    width: 70%;
    border-bottom: 50px;
}

p.card-text, .card-title{
    color: black;
}


a:hover {
    color: black;
    text-decoration: none;
}

.img-card{
    -webkit-filter: grayscale(100);
            filter: grayscale(100);
    -webkit-transition: 0.5s;
    transition: 0.5s;   
}

.img-card:hover{
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}
